@import './Links.module.scss';

.mobilenav > * {
	display: none;
}

.link_mobile {
	@extend .link;
}

@media screen and (max-width: 660px) {
	.mobile {
		z-index: 2;
		display: flex;
		position: relative;
		justify-content: space-between;
		align-items: center;
		height: 3.5rem;
		background-color: white;
	}

	.mobilenav > nav {
		display: flex;
	}
}
